body{
  margin: 5px;
  font-family: 'righteous',cursive;
  color:brown;
  line-height: 1.7;
  display: flex;
  background-image: url("Nasa.jpg");
  margin-bottom: 25rem;    

}
button{
  background-color: steelblue;
  background-origin: padding-box
  border-box;
  border-radius: 15px;
}
input{
  
  width:200px;
  height: 30px;
  margin: 25px;
  margin-left: 200px;
  background:steelblue;
  background-image: url("calender.png");
  background-size: 30px;
  background-repeat: no-repeat;
  background-position: 90%;
  padding: 5px;
  font-size: 18px;
  font-weight: 999;
  border: none;
  outline: none;
  
}
  input:focus{
border: 5px solid goldenrod;
  }
  input[type="date"]
  ::-webkit-calendar-picker-indicator{
    opacity: 0;
  }

img{
  display: flex;
}
.box1,.box2,.box3,.box4{
  
    
  background-color:steelblue;
  border-radius: 10px 10px 10px 10px;
  text-shadow: 0px -1px 0px grey;

  
  height: 50px;
  
  
  box-shadow: 5px 5px 5px red ;
}
.box1{
  width: 75px;
  
}
.box2{width: 110px;}
.box3{width: 150px;}
.box4{width: 110px;}

.grid{
  
  min-height: 50%;
  display: flex;
  flex-wrap: wrap;
  flex-direction:row;

}
.grid > div{
  flex-basis: calc(33.3333%-40%);
}
.box{
  
  margin: 15px;
  margin-top: 35px;
  margin-left: 30px;
  margin-right: 30px;
}
.home{
  color:yellow
}
